import { Button } from "reactstrap";
import { Link, useHistory } from "react-router-dom";
import notAuthImg from "@src/assets/images/pages/unauthorized-access.png";

import "../../../@core/scss/base/pages/page-misc.scss";
import PageTitle from "../../customizedComponents/PageTitle";

const NotAuthorized = () => {
  const history = useHistory();
  return (
    <>
      <PageTitle title="غير مصرّح بالدخول" />
      <div className="misc-wrapper">
        <div className="misc-inner p-2 p-sm-3">
          <div className="w-100 text-center">
            <h2 className="mb-1" dir="rtl">
              غير مصرّح لك بالدخول 🔐
            </h2>
            <p className="mb-2 text-primary" dir="rtl">
              لا تملك الصلاحيات الكافية للولوج إلي هذه الصفحة!
            </p>

            <div
              style={{
                // display: "grid",
                alignItems: "center",
                display: "flex",
                flexDirection: "column",
              }}
            >
              <img
                className="img-fluid"
                src={notAuthImg}
                alt="Not authorized page"
                style={{ paddingBottom: "10px" }}
              />
              <Button
                color="primary"
                style={{ width: "100%" }}
                className="btn-sm-block mb-2"
                onClick={() => {
                  history.goBack();
                }}
              >
                العودة إلي الصفحة السابقة
              </Button>
              <Button
                tag={Link}
                to="/"
                color="secondary"
                // style={{ width: "75%" }}
                className="btn-sm-block mb-1"
              >
                العودة إلي الصفحة الرئيسية
              </Button>
            </div>
          </div>
        </div>
      </div>
    </>
  );
};
export default NotAuthorized;
